div.p-0.container-fluid {
  display: flex;
  height: 3.5em;
  width: 100%;
  background-color: rgb(36, 41, 51);
  box-shadow: rgba(36, 41, 51, 0.15) 0px 5px 10px 0px;
}
.navbar-light .navbar-brand {
  color: rgb(236, 239, 244) !important;
  font-weight: 600 !important;
}

.navbg.navbar-dark.navbar.navbar-expand-lg.navbar-light.fixed-top {
  padding-right: 70px !important;
  padding-left: 70px !important;
}

.navbg {
  background-color: rgb(46, 52, 64);
}

.navbar-light .navbar-nav a .nav-link {
   color: rgb(236, 239, 244) !important;
  font-size: 1.200em !important;
  font-weight: 600 !important;

}

.navbar-light .navbar-nav a:nth-child(-n+3) {
  padding-right: 10px;
}
.navbar-light .navbar-nav a:hover {
   -webkit-transition: all .2s ease-in-out;
   transition: all .2s ease-in-out; 
   
}
.navbar-light .navbar-nav a .nav-link:hover{
  border-bottom: solid 1px rgb(59, 63, 71);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
} 



@media only screen and (max-width: 768px){
  .navbar-dark .navbar-nav a .nav-link {
    text-align: center !important;
    margin-left: 0 !important;
    padding: 4px 0px !important;
  }

  .navbar-light .navbar-nav a .nav-link:nth-child(-n+3) {
     padding-right: 0px !important; 
}

  .navbar-light .navbar-brand {
    font-size: 32px !important;
  }
  .navbg.navbar-dark.navbar.navbar-expand-lg.navbar-light.fixed-top {
     padding-right: 20px !important;
     padding-left: 20px !important; 
  } 
  .navbar-dark .navbar-toggler {
    padding: 4px !important;
  }
  .navbar {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
  }
}
.fixed-bottom{
    background-color: rgb(46, 52, 64);
    margin-top: 50px;
}

div.col#herotext *{
    display: flex;
    color: rgb(202, 204, 209);
    justify-content: center;
}
p.display-5{
    font-size: 20px;
}



button.btn.btn-primary{
    background-color: #38706b !important;
    border:solid 4px #38706b !important;
    outline: none;
    display:block;
    margin-top: 20px;
    border-color: none;
}
a.btn{
    background-color: #38706b !important;
    border:solid 4px #38706b !important;
}

div#secondrow.row{
    display: flex;
    justify-content: center;
    margin-top: 20px;
}
div#secondrow.row *{
    margin: 5px;
}


button#first.btn.btn-primary.btn-lg:hover{
    background-color: #2e5e5a !important;
    border:solid 4px #2e5e5a !important;

}

button#second.btn.btn-primary.btn-lg{
    background: transparent !important;
    border:solid 3px #455566 !important;
}
button#second.btn.btn-primary.btn-lg:hover{
    background: transparent !important;
    border:solid 3px #596c80 !important;
    color: rgb(179, 182, 187);
}

